@import url('./_vars.css');

.card{
    width: 40%;
    background-color: white;
    display:flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 0px 9px -1px rgba(0,0,0,0.83);
    border-radius: 10px;
    overflow: hidden;
    padding-bottom: 10px;
}
.card__img{
    width: 100%;
    height: 200px;
    background-color: var(--lightPrimaryColor);
    overflow: hidden;
    object-fit: cover;
    position:relative;
}

.card img{
    width: 150%;
    position: absolute;
    bottom:0;
}

.card h4{
    font-size: 1.5rem;
    text-transform: capitalize;
}

.card p{
    color: var(--lighBlack)
}

.card button{
    font-size: 1rem;
    padding: 8px 30px;
    margin-top:5px;
    text-transform: capitalize;
}


@media(max-width: 800px){
    .card{
        height: 250px;
    }

}

@media(max-width: 650px){
    .card{
        width: 100%;
    }

}